.tyn-logo{
    height: 2.75rem;
    padding: .625rem .75rem;
    display: block;
    border-bottom: 1px solid $light;
    svg{
        height: 100%;
    }
}

.tyn-menu{
    display: flex;
    flex-direction: column;
    padding-top: 1rem;
    &-item{
        padding: .25rem .75rem;
        a{
            color: $gray-500;
            padding: .75rem 0;
            font-size: 13px;
            font-weight: 600;
            &:hover{
                color: $primary;
            }
        }
    }
    &-heading{
        padding: .25rem .75rem .5rem;
    }
}

@media (min-width:778px) {
    .tyn-logo{
        height: 4rem;
        padding: 1rem 1.5rem;
    }

    .tyn-menu{
        padding-top: 1.5rem;
        &-item{
            padding: .25rem 1.5rem;
            a{
                padding: .75rem 0;
            }
        }
        &-heading{
            padding: .25rem 1.5rem .75rem;
        }
    }
}