.tyn-main{
    display: flex;
    padding-left: 180px;
}

.tyn-sidebar{
    width: 180px;
    background: $white;
    min-height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    overflow: hidden;
}

.tyn-content{
    padding: 0 .25rem 1.5rem;
    width: 100%;
}

@media (min-width:778px) {
    .tyn-main{
        padding-left: 230px;
    }
    .tyn-sidebar{
        width: 230px;
    }
    .tyn-content{
        padding: 2rem;
    }
}

@media (min-width:992px) {
    .tyn-main{
        padding-left: 270px;
    }
    .tyn-sidebar{
        width: 270px;
    }
}
